 necesario que se establezca un nivel de organización para el monitoreo continuo del funcionamiento del plan de acción. ")]),_c('p',[_vm._v("Está encargado de administrar todos los recursos destinados a las operaciones del proyecto. ")])])])]),_c('div',{staticClass:"row",attrs:{"titulo":"Planificación"}},[_c('div',{staticClass:"col-12"},[_c('p',{staticClass:"texto-acordeon-tema3 mb-0"},[_vm._v("La planificación es una de las funciones fundamentales del PMU mediante la cual se recolecta, analiza y difunde la información del evento al interior de la estructura, la cual realiza el control de recursos operativos.")])])]),_c('div',{staticClass:"row",attrs:{"titulo":"Operaciones"}},[_c('div',{staticClass:"col-12"},[_c('p',{staticClass:"texto-acordeon-tema3 mb-0"},[_vm._v("Están encargados de determinar las estrategias que se aplicarán, así como las necesidades de recursos adicionales que se requieran para la correcta operación del sistema. ")])])]),_c('div',{staticClass:"row",attrs:{"titulo":"Logística"}},[_c('div',{staticClass:"col-12"},[_c('p',{staticClass:"texto-acordeon-tema3 mb-0"},[_vm._v("Los PMU serán los gestores de las instalaciones, servicios y materiales que se puedan necesitar para brindar apoyo a la atención de la emergencia, asegurando el bienestar del personal de respuesta, brindándoles la correcta alimentación y descanso, además deberá suministrar los equipos de comunicación que se requieran. ")])])]),_c('div',{staticClass:"row",attrs:{"titulo":"Administración y finanzas"}},[_c('div',{staticClass:"col-12"},[_c('p',{staticClass:"texto-acordeon-tema3 mb-0"},[_vm._v("Llevará el control de todos aquellos aspectos administrativos que se deban adelantar en el manejo de las finanzas del evento de emergencia. ")])])]),_c('div',{staticClass:"row",attrs:{"titulo":"Seguridad"}},[_c('div',{staticClass:"col-12"},[_c('p',{staticClass:"texto-acordeon-tema3 mb-0"},[_vm._v("El PMU garantiza la seguridad del personal de respuesta, evaluando y controlando las situaciones pe